.app-wrapper{
  margin: 0 auto;
  display: grid;
  width: 1200px;
  grid-template-areas:
 'h h'
 'n c';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 5px;
  /*grid-row-gap: 1em;*/
}
.app-wrapper-content {
    grid-area: c;
    background-color: #232933;
}
.app-wrapper-content span{
    color: white;
    border: 1px solid white;
}
